'use client';



export default function SellerAuthLayout({
  children,
}: {
  children: React.ReactNode;
}) {

  return (
    <div className="min-h-screen grid lg:grid-cols-[40%_60%] bg-white w-full">
      {/* LEFT SIDE */}
      <div className="bg-[#DDFFF4] p-16 flex flex-col justify-between">
        <div>
          <h1 className="text-3xl font-semibold text-black">
            Setup your account
          </h1>

          <p className="mt-2 text-sm text-gray-700">
            Letâ€™s get you up and running immediately.
            <br />
            Kindly proceed to verify and setup your store
          </p>

          <div className="mt-12 space-y-6">
            const pathname = usePathname();

            <Step
              label="Identity verification"
              active={pathname.includes("identity")}
            />

            <Step label="Store setup" />
            <Step label="Account setup" />
            <Step label="Verify" />
          </div>
        </div>

        <div className="bg-[#0D3B2E] text-white p-6 rounded-[20px]">
          <p className="text-sm">
            Verified sellers make 10x more sales than unverified sellers.
            <br />
            You can boost a product to get even better visibility and more sales.
          </p>
        </div>
      </div>

      {/* RIGHT SIDE */}
      <div className="bg-[#F4F4F4] flex items-center justify-center p-20">
        {children}
      </div>
    </div>

  );
}

function Step({
  label,
  active,
}: {
  label: string;
  active?: boolean;
}) {
  return (
    <div className="flex items-center gap-6">
      {/* Circle */}
      <div
        className={`w-12 h-12 rounded-full flex items-center justify-center
        ${active
            ? "border-[3px] border-[#0D3B2E] bg-[#CFEFE6]"
            : "border-[3px] border-[#D9D9D9] bg-[#EDEDED]"
          }`}
      >
        <div
          className={`w-6 h-6 rounded-full ${active ? "bg-[#0D3B2E]" : "bg-[#CFCFCF]"
            }`}
        />
      </div>

      {/* Label */}
      <span
        className={`text-2xl ${active ? "text-black font-medium" : "text-black"
          }`}
      >
        {label}
      </span>
    </div>
  );
}
